.foundation-list {
  max-height: 60vh;
  overflow-y: auto;
  margin-bottom: 32px;

  .foundation-list-item {
    display: flex;
    padding: 10px 18px;

    &:not(:last-child) {
      margin-bottom: 38px;
      border-bottom: 1px solid #d2dae0;
    }

    .foundation-header {
      margin-bottom: 16px;

      .ant-col {
        margin-bottom: 16px;
      }

      .foundation-avatar {
        width: 50px;
        height: 50px;
        border: 1px solid #d2dae0;
        border-radius: 9px;
        font-size: 10px;
        text-overflow: ellipsis;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      .title-container {
        padding-left: 10px;
        .title {
          color: #1f282d;
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
          margin-bottom: 4px;
        }

        .categories {
          display: flex;
          align-items: center;

          .category {
            background: #f0f3f5;
            border-radius: 48px;
            padding: 4px 8px;
            font-weight: 600;
            font-size: 10px;
            line-height: 14px;
            letter-spacing: 0.01em;
            text-transform: uppercase;
            color: #5e7787;
          }
        }
      }
    }

    .foundation-body {
      .description {
        font-weight: 400;
        font-size: 14px;
      }

      button.foundation-more-details {
        padding-inline: 0px !important;
      }
    }
  }
}
