@import "../Onboarding.less";

.onboarding-card {
  .description {
    font-weight: 300;
    font-size: 20px;
    color: #1f282d;
    margin-bottom: 30px;
    span {
      font-weight: bold;
    }
  }

  .footprint-comparison-chart-container {
    .title {
      color: #002f4d;
      font-weight: 400;
      font-size: 14px;

      span {
        font-weight: bold;
      }
    }
    padding: 12px 16px;
    border: 1px solid #f0f3f5;
    box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.08);
    border-radius: 8px;
    margin-bottom: 16px;
  }

  .emission-details-container {
    .ant-col {
      height: 100%;
      margin-bottom: 16px;

      &:not(:last-child) {
        padding-right: 20px;
      }
    }

    .emission-comparison-chart-container {
      padding: 12px 16px;
      border: 1px solid #f0f3f5;
      box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.08);
      border-radius: 8px;
      // margin-bottom: 16px;
      height: 100%;

      .title {
        color: #002f4d;
        font-weight: 400;
        font-size: 14px;

        span {
          font-weight: bold;
        }
      }
    }

    .annual-emission-card {
      padding: 12px 16px;
      background: #cae7d8;
      border-radius: 8px;
      position: relative;
      height: 100%;
      overflow: hidden;

      .title,
      .value {
        position: relative;
        z-index: 10;
        color: #183525;
      }

      .title {
        font-weight: 400;
        font-size: 14px;
        margin-bottom: 12px;
      }

      .value {
        width: 50%;

        h1 {
          font-size: 32px;
          font-weight: 600;
          margin-bottom: 4px;
        }
        p {
          font-size: 16px;
          font-weight: 600;
          margin-bottom: 70px;
        }
      }

      .trees {
        position: absolute;
        display: flex;
        align-items: flex-end;
        z-index: 1;
        right: 4px;
        bottom: 0;

        img.tree {
          &:not(:last-child) {
            margin-right: 18px;
          }
        }
      }
    }

    .message {
      display: flex;
      align-items: flex-start;
      background: #f7f8f9;
      border-radius: 8px;
      padding: 11px 14px;

      .icon {
        font-size: 16px;
        color: #5e7787;
        margin-right: 12px;
      }

      p {
        flex: 1;
        font-weight: 400;
        font-size: 14px;
        color: #5e7787;
        line-height: 22px;
        margin: 0px;
      }
    }
  }
}
