.connect-bank-account-button {
  .disclaimer {
    display: flex;
    justify-content: center;
    align-self: center;
    margin-block: 12px;

    small {
      font-size: 10px;
      font-weight: 700;
      color: #96a9b6;

      &:not(:last-child) {
        margin-right: 18px;
        position: relative;

        &::after {
          content: "";
          position: absolute;
          background-color: #96a9b6;
          width: 3px;
          aspect-ratio: 1;
          border-radius: 100%;
          transform: translateY(-50%);
          top: 50%;
          right: -10px;
        }
      }
    }
  }
}
