.split-donation-foundation-item {
  background: #ffffff;
  border: 1px solid #f0f3f5;
  box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.04);
  border-radius: 8px;
  padding: 16px;
  margin-bottom: 16px;

  .content {
    .image-container {
      width: 50px;
      height: 50px;
      border: 1px solid #d2dae0;
      border-radius: 9px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .title {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #1f282d;
    }
  }

  .value-container {
    span {
      font-weight: 600;
    }
  }
}
