.chart-labels {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-block: 10px;

  .chart-label {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 12px;
    color: #5e7787;

    &:not(:last-child) {
      margin-right: 24px;
    }

    .icon {
      width: 12px;
      height: 12px;
      border-radius: 100%;
      margin-right: 4px;
    }
  }
}
