.featured-foundation {
  border: 1px solid #f0f3f5;
  box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.04);
  border-radius: 8px;
  background: white;
  overflow: hidden;
  height: 384px;

  .banner {
    width: 100%;
    height: 122px;
    background: #1f282d;
    text-align: center;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      color: white;
      object-fit: cover;
    }
  }

  .body {
    padding: 16px 24px;

    .title {
      font-weight: bold;
      font-size: 20px;
      line-height: 26px;
      color: #1f282d;
      margin-bottom: 8px;
    }

    .categories {
      display: flex;
      align-items: center;
      margin-bottom: 16px;

      .category {
        background: #f0f3f5;
        border-radius: 48px;
        padding: 4px 8px;
        font-weight: 600;
        font-size: 10px;
        line-height: 14px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        color: #5e7787;
        text-align: center;
      }
    }

    .description {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #5e7787;
      margin: 0px;
      display: block; /* or inline-block */
      text-overflow: ellipsis;
      word-wrap: break-word;
      overflow: hidden;
      max-height: 8.6em;
    }
  }
}
