.image-viewer {
  position: fixed;
  background: rgba(31, 40, 45, 0.75);
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;

  .close-button {
    position: absolute;
    cursor: pointer;
    top: 2%;
    right: 2%;
    padding: 8px;
    color: white;
    font-size: 30px;
  }

  .image-slider-row {
    width: 85vw;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .image-splide {
      width: 85vw;
      height: 80vh;

      .splide__arrows {
        .splide__arrow {
          width: 2rem;
          height: 2rem;

          &.splide__arrow--prev {
            left: -1.2rem;
          }

          &.splide__arrow--next {
            right: -1.2rem;
          }
        }
      }

      .splide__track {
        .splide__list {
          .splide__slide {
            .image-slide {
              width: 85vw;
              height: 80vh;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;

              .image-container {
                height: 100%;
                flex: 1;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                position: relative;
                border-radius: 8px;
                overflow: hidden;
                background: rgba(0, 0, 0, 0.5);

                img {
                  width: 100%;
                  height: 100%;
                  object-fit: contain;
                  cursor: pointer;
                }

                .message {
                  padding: 1rem 2rem 2rem 2rem;
                  color: #ffffff;
                  background: rgba(0, 0, 0, 0.5);
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  right: 0;

                  p {
                    overflow: hidden;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 20px;
                    margin: 0px;
                  }

                  span {
                    cursor: pointer;
                    text-decoration: underline;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
