.custom-number-step-input {
  display: flex;
  justify-content: center;
  align-items: center;

  button {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #e8edf1;
    border: none;
  }

  .value-container {
    margin-inline: 8px;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: #1f282d;
    display: flex;
    align-items: center;

    input {
      border: none;
      background: transparent;
      min-width: 30px;
      width: 30px;
    }
  }
}
