.splide.foundation-gallery {
  margin-block: 20px;

  .splide__track {
    .splide__list {
      .splide__slide {
        padding-inline: 8px;
        img.gallery-image {
          width: 100%;
          height: 100%;
          object-fit: cover;
          border-radius: 8px;
          cursor: pointer;
        }
      }
    }
  }

  .splide__pagination {
    bottom: -24px;

    li {
      &:not(:last-child) {
        margin-right: 8px;
      }

      .splide__pagination__page {
        background: #495d69;
        opacity: 0.24;

        &.is-active {
          opacity: 1;
        }
      }
    }
  }
}
