.custom-radio-group {
  .radio-button {
    flex: 1;
    width: 100%;
    height: 100%;
    border: 1px solid #ebebec;
    border-radius: 6px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    cursor: pointer;
    transition: all 0.2s;
    position: relative;

    &.selected {
      border-color: #4f9cfd;
      transition: all 0.2s;

      .selected-icon {
        position: absolute;
        top: 6px;
        right: 6px;
        color: #4f9cfd;

        & svg {
          width: 16px;
          height: 16px;
        }
      }
    }
    &:hover {
      border-color: #4f9cfd;
      transition: all 0.2s;
    }
    &:not(:last-child) {
      margin-right: 8px;
    }

    label {
      font-size: 14px;
      font-weight: bold;
      color: #383b46;
    }

    small {
      font-weight: 400;
      font-size: 12px;
      color: #888990;
    }
  }
}
