@import "../Onboarding.less";

.onboarding-card {
  .question-icon {
    margin-bottom: 30px;
  }

  .progress {
    font-weight: 500;
    font-size: 14px;
    color: #888990;
    margin-bottom: 10px;
  }

  .title {
    font-weight: 500;
    font-size: 20px;
    color: #1f282d;
    font-weight: bold;
    margin-bottom: 10px;
  }

  .description {
    font-size: 16px;
    line-height: 24px;
    color: #383b46;
  }

  .form-container {
    margin-block: 10px;
    width: 100%;
  }

  .button-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    button {
      flex: 1;
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
}
