.page-content {
  .foundation-layout {
    max-height: calc(100vh - 112px) !important;
    width: 100% !important;
    background: white;
    overflow: hidden;
    box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
    border-radius: 10px;
    padding: 0px !important;
    margin: 0px !important;

    .sider {
      border-right: 1px solid #f0f0f0;

      .header {
        padding: 24px;

        .title {
          font-weight: bold;
          font-size: 16px;
          line-height: 24px;
          color: #2a2d33;
          margin: 0px;
        }
      }

      .body {
        max-height: calc(calc(100vh - 112px) - 80px);
        overflow-y: auto;

        ul.foundation-categories {
          padding: 0px;

          li.category-item {
            cursor: pointer;
            list-style-type: none;
            padding: 9px 24px;
            color: rgba(0, 0, 0, 0.85);

            &:hover {
              background: #e5f5ff;
              color: #008ee6;
            }
            &.selected {
              font-weight: bold;
              background: #e5f5ff;
              color: #008ee6;
              position: relative;

              &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 0;
                height: 100%;
                background: #008ee6;
                width: 3px;
              }
            }
          }
        }
      }
    }

    .content {
      .header {
        padding: 24px;

        .title {
          font-weight: bold;
          font-size: 16px;
          line-height: 24px;
          color: #2a2d33;
          margin: 0px;
        }

        .search-foundations {
          input {
            border-start-start-radius: 25px;
            border-end-start-radius: 25px;
          }
          button {
            border-start-end-radius: 25px;
            border-end-end-radius: 25px;
          }
        }
      }

      .body {
        max-height: calc(calc(100vh - 112px) - 80px);
        overflow-y: auto;
        padding-inline: 24px;

        .splide.featured-foundation-splide {
          .splide__track {
            .splide__list {
              .splide__slide {
                padding-inline: 8px;
              }
            }
          }
        }

        .foundations-container {
          .title {
            font-weight: bold;
            font-size: 16px;
            line-height: 24px;
            color: #2a2d33;
            margin-block: 24px;
          }
        }
      }
    }
  }
}
