.total-emissions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
  border-radius: 10px;
  width: 100%;
  overflow: hidden;

  .body {
    padding: 24px;
    height: 100%;

    .header {
      margin-bottom: 16px;

      .co2-cloud {
        width: 64px;
        aspect-ratio: 1;
        margin-bottom: 10px;
      }

      h3.title {
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        text-transform: uppercase;
        color: #5e7787;
        margin-bottom: 5px;
      }

      h1.value {
        font-weight: 400;
        font-size: 32px;
        line-height: 42px;
        color: #2a2d3344;
        margin-bottom: 8px;

        span {
          font-weight: bold;
          color: #2a2d33;
        }
      }
    }
  }

  .body {
    // .value {
    //   font-weight: 600;
    //   font-size: 32px;
    //   color: rgba(42, 45, 51, 0.45);
    //   margin-bottom: 8px;

    //   span {
    //     color: rgba(42, 45, 51, 1);
    //   }
    // }

    .description {
      font-weight: 400;
      font-size: 14px;
      color: #1f282d;
      margin-top: 20px;
    }

    .donate-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 500 !important;
      font-size: 16px !important;
      padding: 10px !important;

      .icon {
        margin-left: 10px;
      }
    }
  }

  .footer {
    padding: 8px 24px;
    background-color: #eef8ff;
    width: 100%;

    p {
      color: #0d5e92;
      margin: 0px;

      span {
        font-weight: bold;
      }
    }
  }
}
