.amount-item-row {
  background: #ffffff;
  border: 1px solid #f0f3f5;
  box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.04);
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;

  &:not(:last-child) {
    margin-bottom: 16px;
  }

  .checkbox-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .content-container {
    .title {
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #1f282d;

      span {
        color: #5e7787;
      }
    }

    p {
      margin: 0px;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #5e7787;
    }
  }

  .amount-container {
    .amount {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #1f282d;
    }
  }
}
