.payment-details-card {
  width: "100%";
  padding: 20px;
  background: #f0f3f5;
  border-radius: 8px;

  .header {
    padding: 0px !important;
    padding-block: 8px !important;
    border-bottom: 1px solid #d2dae0;

    .title {
      margin: 0px;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
    }
  }

  .body {
    .payment-item {
      padding-block: 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  .footer {
    padding: 0px !important;
    padding-block: 8px !important;
    border-top: 1px solid #d2dae0;

    h3 {
      margin: 0px;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
    }
  }
}
