.footprint-breakdown {
  background: #ffffff;
  box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
  border-radius: 10px;
  width: 100%;
  margin-bottom: 16px;

  & .header {
    padding: 24px 24px 0px 24px;

    & .title {
      font-weight: 500;
      font-size: 16px;
      text-transform: capitalize;
      color: #2a2d33;
    }
  }
  & .body {
    padding: 24px;

    & .list {
      & .item {
        background: #ffffff;
        border: 1px solid #f0f3f5;
        box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.04);
        border-radius: 8px;
        padding: 16px;

        & .header {
          padding: 0px;
          display: flex;
          justify-content: space-between;
          align-items: flex-start;

          & div {
            display: flex;
            align-items: center;

            &:first-child {
            }
            &:last-child {
              display: flex;
              flex-direction: column;
              justify-content: flex-end;
            }

            & .icon {
              width: 12px;
              height: 12px;
              border-radius: 4px;
              margin-right: 10px;
            }
            & .title {
              font-weight: 600;
              font-size: 16px;
              color: #1f282d;
            }
            & .weight {
              font-weight: 400;
              font-size: 14px;
              color: #1f282d;
            }
            & .value {
              font-weight: 600;
              font-size: 14px;
              color: #1f282d;
            }
          }
        }

        & .description {
          margin: 0px;
          color: #5e7787;
        }
      }
    }

    & .btn-container {
      & button {
        font-weight: 600;
        font-size: 16px;
        margin-top: 12px;
        height: 40px;
      }
    }
  }
}
