.options {
  display: flex;
  flex-wrap: wrap;

  label.option {
    font-weight: 400;
    font-size: 16px;
    color: #1f282d;
    border: 2px solid #d2dae0;
    border-radius: 45px;
    text-align: center;
    padding: 14px 16px;
    margin-right: 8px;
    margin-bottom: 22px;
    position: relative;

    &.selected {
      border: 2px solid #4f9cfd;
      transition: all 0.2s;

      .selected-icon {
        position: absolute;
        top: 0px;
        right: 0px;
        color: #4f9cfd;
        background: white;
        border-radius: 100%;
        width: 20px;
        height: 20px;

        & svg {
          width: 20px;
          height: 20px;
        }
      }
    }

    &:hover {
      cursor: pointer;
      border: 2px solid #4f9cfd;
      transition: all 0.2s;
    }
  }
}
