.page-content {
  .donation-layout {
    height: calc(100vh - 112px);
    width: 100% !important;
    background: white;
    overflow: hidden;
    box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
    border-radius: 10px;
    padding: 0px !important;
    margin: 0px !important;

    .ant-layout-sider {
      padding: 24px;
      min-width: 260px !important;
      width: 260px !important;
      max-width: 260px !important;
      border-right: 1px solid #f0f0f0;

      .ant-layout-sider-children {
        height: 100% !important;

        .donation-menu.ant-menu {
          border-inline-end: 0px !important;

          .ant-menu-item {
            position: relative;
            overflow: visible;
            z-index: 1;
            margin-bottom: 20px;

            &.ant-menu-item-selected {
              background-color: transparent;

              .icon {
                background: #4f9cfd;

                &::after {
                  content: "";
                  position: absolute;
                  width: 40px;
                  height: 40px;
                  border-radius: 40px;
                  transform: translate(-50%, -50%);
                  left: 50%;
                  top: 50%;
                  background: rgba(79, 156, 253, 0.16);
                }
              }
              .ant-menu-title-content {
                font-weight: bold;

                span {
                  color: #4f9cfd !important;
                }
              }
            }

            &:hover {
              .ant-menu-title-content {
                text-decoration: underline;
              }
              .icon {
                background: #4f9cfd;
              }
            }

            &:not(:last-child) {
              ::before {
                content: "";
                position: absolute;
                width: 3px;
                height: 35px;
                left: 38px;
                top: 30px;
                background: #d7d8da;
              }
            }

            .icon {
              position: relative;
              background: #d7d8da;
              border-radius: 50%;
              display: flex;
              justify-content: center;
              align-items: center;
              width: 30px !important;
              height: 30px !important;
              z-index: 10;
              color: white;

              &.completed {
                background: #34424c;
              }
            }

            .ant-menu-title-content {
              color: #d7d8da;

              span.completed {
                color: #34424c;
              }
            }
          }
        }
      }
    }

    .ant-layout-content {
      background: white;

      .header {
        padding: 24px;

        .title {
          font-weight: bold;
          font-size: 16px;
          line-height: 24px;
          color: #2a2d33;
          margin: 0px;
        }
      }
    }

    .ant-layout-footer {
      padding: 0px;

      .total-footer {
        background: white;
        padding: 18px 24px;
        border-top: 1px solid #f0f0f0;

        .total-container {
          p {
            font-weight: 600;
            font-size: 10px;
            line-height: 14px;
            letter-spacing: 0.01em;
            text-transform: uppercase;
            color: #5e7787;
          }
          h3 {
            margin: 0px;
            font-weight: 600;
            font-size: 20px;
            line-height: 26px;
            text-align: center;
            color: #1f282d;
          }
        }
      }
    }
  }
}
