.foundation {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  background-color: white;
  box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.04);
  width: 100%;
  padding: 16px 24px;
  border-radius: 8px;
  margin: auto;
  margin-bottom: 20px;
  height: 100%;

  .header {
    padding: 0 !important;
    margin-bottom: 16px;

    .title-container {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 0 !important;
      margin-bottom: 21px;

      .icon {
        width: 50px;
        height: 50px;
        border: 1px solid var(--primary-green);
        border-radius: 8px;
        margin-right: 10px;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .title {
        font-size: 20px;
        font-weight: 600;
        margin: 0px !important;
      }
    }

    .type-container {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      margin-block: 10px;

      .type {
        background: #f3f3f3;
        border-radius: 48px;
        padding: 4px 8px;
        font-weight: 600;
        font-size: 10px;
        line-height: 14px;
        color: #677275;
        text-transform: uppercase;
      }
    }
  }

  .description {
    max-height: 128px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #5e7787;
    display: block; /* or inline-block */
    text-overflow: ellipsis;
    word-wrap: break-word;
    overflow: hidden;
    max-height: 8em;
  }

  .footer {
    .location {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #5e7787;

      svg {
        margin-right: 8px;
        color: #373f41;
      }
    }
  }
}
