.filter {
  & .item-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0px;
    background: #f0f3f5;
    border-radius: 24px;

    & .item {
      cursor: pointer;
      flex: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 500;
      font-size: 13px;
      height: 40px;
      color: #898a8c;

      &.selected {
        color: #323040;
        background: #ffffff;
        box-shadow: 0px 1px 2px rgba(34, 34, 38, 0.15),
          0px 4px 6px rgba(34, 34, 38, 0.08);
        border-radius: 24px;
      }
    }
  }
}
