&.ant-layout-sider-collapsed {
  .ant-layout-sider-children {
    .bottom-container {
      .container {
        .account-setup {
          overflow: hidden;
          text-overflow: clip;
          padding: 16px 5px;

          .header {
            text-overflow: clip;
            position: relative;

            .title {
              text-align: center;
            }

            .anticon {
              position: absolute;
              opacity: 0;
            }
          }
          .body {
            .percent {
              text-align: center;
              position: relative;

              span {
                position: absolute;
                opacity: 0;
              }
            }

            .message-container {
              justify-content: center;
              position: relative;

              .anticon {
                margin-right: 0px !important;
              }

              p {
                position: absolute;
                opacity: 0;
              }
            }
          }
        }
      }
    }
  }
}
