.feed-list {
  .feed-filter {
    background-color: #fff;
    box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
    border-radius: 10px;
    padding: 24px;
    margin-bottom: 20px;

    & .title {
      font-weight: 600;
      font-size: 16px;
      margin: 0px;
    }
  }

  .feed {
    display: flex;
    flex-direction: column;
    background: #ffffff;
    box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 16px;

    & .header {
      padding: 16px;

      .author {
        display: flex;
        & .avatar {
          width: 56px;
          height: 56px;
          border: 1px solid #d2dae0;
          border-radius: 9px;
          margin-right: 12px;

          & img {
            width: 100%;
          }
        }
        & .title {
          display: flex;
          flex-direction: column;
          justify-content: center;
        }
        & h4 {
          font-weight: 600;
          font-size: 16px;
        }
        & small {
          font-weight: 400;
          font-size: 12px;
          color: #7d91a2;
        }
      }
    }

    & .body {
      padding: 16px;
      & p {
        color: #5e7787;
      }
      & p span {
        font-weight: 600;
        color: black;
      }
    }

    & .image {
      width: 100%;
    }

    & .footer button {
      border: 0px !important;
      border-radius: 0px !important;
      flex: 1;
    }
  }
}
