.donation-layout {
  .month-tabs.ant-tabs {
    .ant-tabs-nav {
      margin: 0px;

      .ant-tabs-nav-wrap {
        padding-inline: 24px;
      }
    }

    .ant-tabs-content-holder {
      .ant-tabs-content {
        .ant-tabs-tabpane {
          padding: 24px;
          overflow-y: auto;
          max-height: calc(100vh - 320px);

          .message {
            display: flex;
            align-items: center;
            margin-bottom: 24px;

            .trophy-icon {
              display: flex;
              justify-content: center;
              align-items: center;
              font-size: 20px;
              width: 35px;
              height: 35px;
              color: #d8a30d;
              background: #fef8e7;
              border-radius: 50%;
              margin-right: 10px;
            }

            p {
              margin: 0px;
              font-weight: 400;
              font-size: 14px;
              line-height: 20px;
              color: #5e7787;
            }
          }

          .ant-form-item-row {
            .ant-form-item-control {
              flex-direction: column-reverse;
            }
          }
          .custom-amount {
            width: 100%;
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #ffffff;
            border: 1px solid #f0f3f5;
            box-shadow: 0px 4px 8px rgba(0, 47, 77, 0.04);
            border-radius: 8px;
            padding: 16px;

            .amount {
              font-weight: 600;
              font-size: 16px;
              line-height: 24px;
            }
          }
        }
      }
    }
  }
}
