.ant-layout-sider {
  &.ant-layout-sider-collapsed {
    .footprint-sidemenu {
      .ant-menu {
        .ant-menu-item {
          padding-inline: calc(50% - 12px) !important;
        }
      }
    }
  }
  .footprint-sidemenu {
    padding: 16px;
    overflow: hidden;

    .progress-box {
      background: #295069;
      border-radius: 8px;
      padding: 16px;
      margin-bottom: 20px;

      .header {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        font-size: 14px;
        color: white;

        h4 {
          font-weight: 400;
        }
      }

      .body {
        padding-top: 20px;

        .progress-bar {
          position: relative;
          width: 100%;
          height: 4px;
          border-radius: 4px;
          background: rgba(0, 0, 0, 0.24);
          margin-bottom: 10px;
          overflow: hidden;

          .progress {
            position: absolute;
            left: 0px;
            top: 0px;
            height: 100%;
            background: #4f9cfd;
            border-radius: 4px;
          }
        }

        p {
          font-weight: 400;
          font-size: 12px;
          color: white;
        }
      }
    }

    .ant-menu {
      .ant-menu-item {
        margin: 0px !important;
        margin-bottom: 10px !important;
        padding: 5px 0px !important;
        position: relative;
        overflow: visible;
        z-index: 1;

        .anticon {
          position: relative;
          background: #295069;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 30px !important;
          height: 30px !important;
          z-index: 10;

          &.completed {
            background: #34424c;
          }
        }

        .ant-menu-title-content {
          color: white;
          font-weight: 600;
          font-size: 14px;
        }

        &.ant-menu-item-selected {
          background-color: transparent;

          .anticon {
            background: #4f9cfd;

            &::after {
              content: "";
              position: absolute;
              width: 40px;
              height: 40px;
              border-radius: 40px;
              transform: translate(-50%, -50%);
              left: 50%;
              top: 50%;
              background: rgba(79, 156, 253, 0.16);
              // background-color: red;
            }
          }
          .ant-menu-title-content {
            font-weight: bold;
          }
        }

        &:hover {
          .ant-menu-title-content {
            text-decoration: underline;
          }
          .anticon {
            background: #4f9cfd;
          }
        }

        &:not(:last-child) {
          ::before {
            content: "";
            position: absolute;
            width: 3px;
            height: 35px;
            left: 8%;
            top: 30px;
            background: #295069;
          }
        }
      }
    }

    .menu {
      list-style-type: none;
      padding: 0px;

      li.footprint-sidemenu-item {
        color: white;
        font-weight: 600;
        font-size: 14px;
        margin-bottom: 10px;
        padding-block: 8px;
        border-radius: 4px;
        position: relative;
        cursor: pointer;

        &:hover {
          text-decoration: underline;
          & span.icon {
            background: #4f9cfd;
          }
        }

        &:not(:last-child) {
          &::after {
            content: "";
            position: absolute;
            width: 3px;
            height: 35px;
            left: 14px;
            top: 30px;
            background: #295069;
            z-index: 1;
          }
        }

        span.icon {
          width: 30px;
          height: 30px;
          border-radius: 30px;
          background: #295069;
          margin-right: 22px;
          justify-content: center;
          align-items: center;
          position: relative;
          z-index: 10;
        }

        &.completed span.icon {
          background: #34424c;
        }

        &.selected span.icon {
          background: #4f9cfd;

          &::after {
            content: "";
            position: absolute;
            width: 40px;
            height: 40px;
            border-radius: 40px;
            background: rgba(79, 156, 253, 0.16);
          }
        }
      }
    }
  }
}
