.styled-select-wrapper {
  position: relative;

  .prefix-icon-wrapper {
    position: absolute;
    z-index: 1;
    width: 3rem;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #7891a1;
    font-size: 20px;
  }

  & .ant-select.has-icon .ant-select-selector span {
    padding-left: calc(3rem - 8px);
  }
}
