.message-container {
  padding: 25px;
  background: #1f282d;
  border-radius: 10px;
  width: 100%;

  .container {
    display: flex;

    .icon {
      width: 50px;
      height: 50px;
      background: #495d69;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      margin-right: 20px;
    }
    .content {
      flex: 1;
      .title {
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: white;
      }
      .description {
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: white;
        opacity: 0.75;
      }
    }
  }
}
