.foundation-details {
  padding: 25px;
  background: white;
  box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
  border-radius: 10px;

  .header {
    margin-bottom: 18px;

    .title-container {
      display: flex;
      align-items: center;

      .go-back-arrow {
        cursor: pointer;
        color: #008ee6;
        font-size: 20px;
        margin-right: 17px;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      .title {
        font-weight: bold;
        font-size: 20px;
        line-height: 28px;
        color: rgba(0, 0, 0, 0.85);
        margin: 0px;
        margin-right: 8px;
      }

      .category {
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50px;
        background: #f0f3f5;
        padding: 4px 8px;
        color: #5e7787;
        font-weight: 600;
        font-size: 10px;
        line-height: 14px;
        text-transform: uppercase;
      }
    }
  }

  .body {
    .image-container {
      width: 100%;
      height: 300px;
      border: 1px solid #ccd4d9;
      border-radius: 8px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .description {
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: #34424b;
    }

    .contact-details {
      .title {
        font-weight: bold;
        font-size: 20px;
        line-height: 26px;
        color: #1f282d;
        margin-bottom: 12px;
      }
      .email {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        text-decoration-line: underline;
        color: #495d69;
        margin-right: 16px;
      }

      .social-media-button {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background: #d2dae0;
        color: #34424b;
        margin-right: 16px;
        transition: all 0.2s;

        &:hover {
          background: #008ee6;
          color: white;
          transition: all 0.2s;
        }
      }
    }

    .gallery-row {
      margin-top: 20px;
    }
  }
}
