@import "../Onboarding.less";

.onboarding-card {
  .title {
    font-weight: bold;
    font-size: 20px;
    color: #1f282d;
  }

  .description {
    font-weight: 500;
    font-size: 16px;
    color: #383b46;
    margin-bottom: 30px;
  }

  .item {
    .header {
      display: flex;
      margin-bottom: 0.5em;

      .icon {
        width: 52px;
        height: 52px;
        margin-right: 18px;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .title {
        margin-bottom: 0px;
      }
    }
  }

  .buttons-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    .disclaimer {
      display: flex;
      justify-content: center;
      align-self: center;
      margin-block: 12px 32px;

      small {
        font-size: 10px;
        font-weight: 700;
        color: #96a9b6;

        &:not(:last-child) {
          margin-right: 18px;
          position: relative;

          &::after {
            content: "";
            position: absolute;
            background-color: #96a9b6;
            width: 3px;
            aspect-ratio: 1;
            border-radius: 100%;
            transform: translateY(-50%);
            top: 50%;
            right: -10px;
          }
        }
      }
    }
  }
}
