.form-column {
  & .container {
    display: flex;
    flex-direction: column;
    padding-inline: 16px;

    & .logo {
      width: 100px;
      margin-bottom: 20px;
    }

    & .title {
      font-weight: bold;
      font-size: 30px;
      color: #383b46;
      margin-bottom: 40px;
    }
    & .login-form-button {
      margin-bottom: 16px;
    }
  }
}

.banner-column {
  & .banner-image {
    width: 90%;
    position: relative;
    margin-right: auto;
    margin-top: auto;

    & .house-image {
      width: 100%;
    }
    & .house-leaf-image {
      position: absolute;
      left: -13%;
      top: 18%;
      width: 29%;
    }
  }
}

@media screen and (max-width: 991px) {
  .banner-column {
    display: none;
  }
}
