.ant-layout-sider {
  &.ant-layout-sider-dark {
    background: #002f4d;

    .ant-menu {
      background: #002f4d;

      .ant-menu-item {
        .ant-menu-item-icon {
          width: 18px;
          height: 18px;
        }
        .ant-menu-title-content {
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
          color: white;
        }
      }
    }
  }

  &.ant-layout-sider-collapsed {
    .ant-layout-sider-children {
      .bottom-container {
        .container {
        }
      }
    }
  }

  .ant-layout-sider-children {
    height: 100vh !important;
    position: relative;

    * {
      transition: opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), margin 0.3s,
        color 0.3s;
    }

    .bottom-container {
      position: absolute;
      transform: translateY(0%);
      bottom: 0px;
      width: 100%;

      .container {
        padding: 16px;

        .account-setup {
          background: #295069;
          border-radius: 8px;
          padding: 16px;
          margin-bottom: 20px;

          .header {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            font-size: 14px;
            color: white;

            .title {
              font-weight: 400;
            }
          }

          .body {
            padding-top: 20px;

            .progress-bar {
              position: relative;
              width: 100%;
              height: 4px;
              border-radius: 4px;
              background: rgba(0, 0, 0, 0.24);
              margin-bottom: 10px;
              overflow: hidden;

              .progress {
                position: absolute;
                left: 0px;
                top: 0px;
                height: 100%;
                background: #4f9cfd;
                border-radius: 4px;
              }
            }

            .percent {
              margin-bottom: 20px;
            }

            .message-container {
              display: flex;
              align-items: flex-start;
              color: white;

              .icon {
                margin-right: 8px;
                font-size: 20px;
              }
            }
            p {
              font-weight: 400;
              font-size: 12px;
              color: white;
            }
          }
        }

        .user-profile-menu {
          border-radius: 8px;
          overflow: hidden;
          background: #295069;

          li {
            ul {
              background: #172c3a;
              background: transparent;
            }
          }
        }
      }
      .collapse-container {
        width: 100%;
        height: 40px;
        background: rgba(255, 255, 255, 0.04);
        display: flex;
        align-items: center;
        padding: 0px 16px;

        .collapse {
          cursor: pointer;
          color: white;
        }
      }
    }
  }
}
