.duration-chart-list {
  width: 100%;
  background: white;
  box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
  border-radius: 24px;
  padding: 20px 24px;

  .header {
    .title {
      font-weight: 500;
      font-size: 16px;
      line-height: 24px;
      color: #2a2d33;
      font-weight: bold;
