.donations-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ffffff;
  box-shadow: 0px 5px 24px rgba(120, 118, 148, 0.15);
  border-radius: 10px;
  width: 100%;
  padding: 24px;

  .body {
    height: 100%;

    .header {
      margin-bottom: 5px;

      h3.title {
        margin: 0px;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        text-transform: uppercase;
        color: #5e7787;
      }

      .donations-dropdown {
        & > span {
          margin-right: 4px;
          font-weight: 400;
          font-size: 14px;
          line-height: 22px;
        }
        & .anticon {
          margin: 0px;
        }
      }
    }

    h1.value {
      font-weight: 600;
      font-size: 32px;
      line-height: 42px;
      color: #2a2d33;
      margin-bottom: 8px;
    }

    .button-container {
      margin-top: 34px;
    }
  }
}
