.not-found-card {
  .ant-card-head {
    .ant-card-head-wrapper {
      flex-direction: row-reverse;
      align-items: center;

      .ant-card-extra {
        display: flex;
        align-items: center;

        .back-button {
          font-size: 20px;
          margin-right: 10px;
        }
      }
    }
  }
  .ant-card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
