@import "../Onboarding.less";

.onboarding-card {
  .banner {
    width: 100%;
    background-color: #002f4d;
    border-radius: 24px;
    padding-top: 10%;
    margin-bottom: 30px;

    & .banner-image {
      width: 90%;
      position: relative;
      margin-right: auto;
      margin-top: auto;

      & .house-image {
        width: 100%;
      }

      & .house-leaf-image {
        position: absolute;
        left: -13%;
        top: 18%;
        width: 29%;
      }
    }
  }

  .title {
    font-weight: bold;
    font-size: 30px;
    color: #383b46;
    margin-bottom: 30px;
  }

  .description {
    font-weight: 500;
    font-size: 16px;
    color: #383b46;
    margin-bottom: 30px;
  }
}
